import axios from 'axios';
import HttpClient from "./http-client";
import { CREATE_SHOPON_API, UPDATE_SHOPON_API, DELETE_SHOPON_API, ALL_SHOPON_LIST_API, GET_POST, GET_SHOPON_WALL_API, SHOPON_SLUG_AVAILABILITY_API, GET_SHOPON_PRODUCT_API } from './api'
import { CREATE_SHOPON_REDUCER, UPDATE_SHOPON_REDUCER, DELETE_SHOPON_REDUCER, ALL_SHOPON_LIST_REDUCER, GET_SHOPON_WALL_REDUCER, ACTIVE_SHOPON_REDUCER, GET_SHOPON_PRODUCT_REDUCER, LOADING_SHOPON_REDUCER, SHOPON_BRAND_DATA_REDUCER } from '../reducer/keys';
import { TOAST_DISMISS, TOAST_ERROR, TOAST_SUCCESS } from '../utils';
import store from '../store';
import { UPDATE_EXTRA_DATA } from './initActions';




export const CREATE_SHOPON = (name, callback, navigate) => {
    const { dashboardData } = store.getState()
    var obj = { "name": name }
    return (dispatch) => {
        new HttpClient().post(CREATE_SHOPON_API, obj).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                localStorage.setItem('shopOn_id', responseData?.id)
                UPDATE_EXTRA_DATA({ shopOn_id: responseData?.id })
                TOAST_DISMISS()
                TOAST_SUCCESS(responseMessage)
                dispatch({ type: CREATE_SHOPON_REDUCER, payload: responseData });
                dispatch({ type: ACTIVE_SHOPON_REDUCER, payload: responseData?.id });
                dispatch({ type: SHOPON_BRAND_DATA_REDUCER, payload: responseData });
                navigate(`/shoponBio/preview/${localStorage.getItem('shopOn_id')}`)
                callback(responseCode, responseMessage);
            }
        }).catch(error => {
            const { data } = error.response;
            TOAST_DISMISS();
            if (data.responseCode == 404) {
                TOAST_ERROR(data.responseMessage);
                callback(data.responseCode, data.responseMessage);
            }
            else {
                TOAST_ERROR("Something went wrong!")
            }
        });
    }
}




export const UPDATE_SHOPON = (obj, callback) => {
    return (dispatch) => {
        new HttpClient().post(UPDATE_SHOPON_API, obj).then((response) => {
            const { responseData, responseCode, responseStatus } = response.data
            if (responseCode == 200) {
                TOAST_DISMISS()
                TOAST_SUCCESS(responseStatus)
                dispatch({ type: UPDATE_SHOPON_REDUCER, payload: { id: obj.id, responseData } });
                
                callback('success');
            }
        }).catch(error => {
            const { data } = error.response;
            TOAST_DISMISS();
            if (data.responseCode == 404) {
                TOAST_ERROR(data.responseStatus);
                callback(data.responseStatus);
            }
            else {
                callback('error');
               // TOAST_ERROR("Something went wrong!")
            }
        });
    }
}


export const DELETE_SHOPON = (id, callback, navigate) => {
    const { shopOnList } = store.getState().shopOnList
    return (dispatch) => {
        new HttpClient().post(`${DELETE_SHOPON_API}/${id}`).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                TOAST_DISMISS()
                TOAST_SUCCESS(responseMessage)
                dispatch({ type: DELETE_SHOPON_REDUCER, payload: id });
                setTimeout(() => {
                    if (shopOnList && shopOnList?.length > 1) {
                        if (id == localStorage.getItem('shopOn_id')) {
                            if (shopOnList[0]?.id == id) {
                                localStorage.setItem('shopOn_id', shopOnList[1]?.id)
                                dispatch({ type: ACTIVE_SHOPON_REDUCER, payload: shopOnList[1]?.id });
                                UPDATE_EXTRA_DATA({ shopOn_id: shopOnList[1]?.id })
                                navigate(`/shoponBio/preview/${shopOnList[1]?.id}`)
                            }
                            else {
                                localStorage.setItem('shopOn_id', shopOnList[0]?.id)
                                dispatch({ type: ACTIVE_SHOPON_REDUCER, payload: shopOnList[0]?.id });
                                UPDATE_EXTRA_DATA({ shopOn_id: shopOnList[0]?.id })
                                navigate(`/shoponBio/preview/${shopOnList[0]?.id}`)
                            }
                        } 

                    } else {
                        localStorage.setItem('shopOn_id', '')
                        UPDATE_EXTRA_DATA({ shopOn_id: '' })
                        dispatch({ type: ACTIVE_SHOPON_REDUCER, payload: '' });
                        navigate(`/shoponBio/preview/`)
                    }
                }, 1000);
                callback(responseCode, responseMessage);
            }
        }).catch(error => {
            const { data } = error.response;
            TOAST_DISMISS();
            if (data.responseCode == 404) {
                TOAST_ERROR(data.responseMessage);
                callback(data.responseCode, data.responseMessage);
            }
            else {
                TOAST_ERROR("Something went wrong!")
            }
        });
    }
}







