export const GET_ALL_SHOPON_LIST = () => {
    var allwalls = store.getState()?.dashboardData?.dashboarddata?.allWalls
    return (dispatch) => {
        new HttpClient().get(ALL_SHOPON_LIST_API).then((response) => {
            const { responseData, responseCode } = response.data
            if (responseCode == 200) {
                var webData = responseData?.find((x) => x.id == localStorage.getItem('shopOn_id'));
                if(!webData) webData = responseData && responseData[0] ? responseData[0].id : '' 
                else localStorage.setItem('shopOn_id', webData ? webData.id : '')
                dispatch({ type: SHOPON_BRAND_DATA_REDUCER, payload: webData });
                dispatch({ type: ALL_SHOPON_LIST_REDUCER, payload: responseData });
                dispatch({ type: LOADING_SHOPON_REDUCER, payload: true });
                dispatch({ type: ACTIVE_SHOPON_REDUCER, payload: localStorage.getItem('shopOn_id') });
            }
        })
    }
}


export const SHOPON_SLUG_AVAILABILITY = (obj) => {
    return new HttpClient().post(SHOPON_SLUG_AVAILABILITY_API, obj,  { validateStatus: false })
}

export const CHANGE_SHOPON_WALL = (wallId, limit) => {
           return (dispatch) => {
            if(wallId){
                dispatch({ type: LOADING_SHOPON_REDUCER, payload: true });
        localStorage.setItem('shopOnActive_wall', wallId);
        new HttpClient().get(`${GET_SHOPON_WALL_API}/${wallId}/${limit}`).then((response) => {
            const { postData, response_code } = response.data;
            if (response_code == 200) {
                dispatch({ type: GET_SHOPON_WALL_REDUCER, payload: response.data })
            }
            setTimeout(()=>{dispatch({ type: LOADING_SHOPON_REDUCER, payload: false });}, 1000)
            dispatch({ type: ACTIVE_SHOPON_REDUCER, payload: localStorage.getItem('shopOn_id') });
            dispatch(CHANGE_SHOPON_PRODUCT(wallId, limit))
        }).catch((error) => {
            console.log("ERROR", error)
            dispatch({ type: LOADING_SHOPON_REDUCER, payload: false });
        });
    };
    };
 
}


export const CHANGE_SHOPON_PRODUCT = (wallId, limit) => {
    return (dispatch) => {
        new HttpClient().get(`${GET_SHOPON_PRODUCT_API}/${wallId}/${limit}`).then((response) => {
            const { responseData, responseCode } = response.data;
            if (responseCode == 200) {
                dispatch({ type: GET_SHOPON_PRODUCT_REDUCER, payload: responseData?.products })
            }
        }).catch((error) => {
            console.log("ERROR", error)
        });
    };
}




