import React, { Component } from 'react'
import { CLOUDPATH } from '../../../../actions/api'
import './linkinbiouser.css' 
import Post from './post'
import Products from './products'

export default class LinkBioPreview extends Component {
    state ={browseDropDown:false, searchDropDown:false, readMore:false, activeTab:1,  productDetail: false, postDetail: false, detailPostData: [], detailProductData: [], activeTab:1}
    

    onBackCall = () => {
        if (this.state.productDetail) this.setState({ browseDropDown: false, postShow: false, productsShow: true, mainPosts: false, postDetail: false, productDetail: false })
        else this.setState({ browseDropDown: false, postShow: false, productsShow: false, mainPosts: true, postDetail: false, productDetail: false })
    }

    detailPostShow = (item) => this.setState({ detailPostData: item, postDetail: true }, () => window.scrollTo(0, 0)) 
    detailProductShow = (item) =>  this.setState({ detailProductData: item, productDetail: true }, () => window.scrollTo(0, 0)) 

    detailPostShow_byref =(postId)=>{
        var getPost = this.props.shopOnUserData?.shopOnPosts?.filter((x)=>x.referenceId == postId)
        if(getPost && getPost[0]){ this.setState({ detailPostData:getPost[0],  postDetail:true, activeTab:1 }, () => window.scrollTo(0, 0)) }
    }

    detailProductShow_byref =(postId)=>{ 
        var getPost = this.props.shopOnUserData?.shopOnProducts?.filter((x)=>x.id == postId) 
        if(getPost && getPost[0]){ this.setState({ detailProductData:getPost[0],  productDetail:true, activeTab:2 }, () => window.scrollTo(0, 0)) } 
    }


    render() {
    const { browseDropDown, searchDropDown, readMore, activeTab, productDetail, postDetail } = this.state
    const { wallData, title, description, profile, productData} =this.props
 
    return (
        <div className='lbUser_embed'>
            <div className='lbUser_header'>
                <div className="lbUserheader_top">
                    <div className='lbUser_profile'>
                        {productDetail || postDetail ?
                            <div className="lb_backbutton" onClick={() => this.onBackCall()}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="20" viewBox="0 0 16 20" fill="none">
                                    <path d="M0 9.93896C0 10.3208 0.145996 10.6465 0.449219 10.9385L9.20898 19.5073C9.44482 19.7544 9.75928 19.8779 10.1187 19.8779C10.8486 19.8779 11.4214 19.3164 11.4214 18.5752C11.4214 18.2158 11.2754 17.8901 11.0283 17.6431L3.1333 9.93896L11.0283 2.23486C11.2754 1.97656 11.4214 1.65088 11.4214 1.2915C11.4214 0.561523 10.8486 0 10.1187 0C9.75928 0 9.44482 0.123535 9.20898 0.370605L0.449219 8.93945C0.145996 9.23145 0.0112305 9.55713 0 9.93896Z" fill="#202223"></path>
                                </svg>
                            </div> : ''}
                        <div className='lbUser_profileouter'>
                        {profile ? profile.includes('ui-avatars.com') ? <img src={`${profile}`} alt="logo" /> : profile.includes('https://cloud.tagshop.ai/') ? <img src={`${profile}`} alt="logo" /> : <img src={`https://cloud.taggbox.com/${profile}`} alt="logo" /> : <div className="noProfile_img"><span>{title?.charAt(0).toUpperCase()}</span></div>}
                        </div>
                        <div className='lb_brand_detail'>
                            <h4 className='lb_title'>{title}</h4>
                            <p className={`lbUser_descrioption ${readMore ? 'active' : ''}`} >
                                {description}
                                {description?.length > 200 ? <div className='lbReadmore' onClick={() => this.setState({ readMore: !readMore })}>Read More</div> : ''}
                            </p>
                        </div>
                    </div>
                </div>
                <div className='lbUser_actions'>

                {productData?.length > 0 ?
                        <div className='shopOnBio_section'>
                            <ul className="nav nav-pills" id="pills-tab" role="tablist">
                                <li className="nav-item" role="presentation">
                                    <button className={`nav-link ${activeTab ==1 ? 'active' :''}`}  id="posts-tab" data-bs-toggle="pill" data-bs-target="#posts" type="button" role="tab" aria-controls="posts" aria-selected="true" onClick={()=>this.setState({activeTab:1})}>Posts</button>
                                </li>
                                <li className="nav-item" role="presentation">
                                    <button className={`nav-link ${activeTab ==2 ? 'active' :''}`}  id="products-tab" data-bs-toggle="pill" data-bs-target="#products" type="button" role="tab" aria-controls="products" aria-selected="false" onClick={()=>this.setState({activeTab:2})}>Products</button>
                                </li>
                            </ul>
                            <div className="tab-content" id="pills-tabContent">
                                <div className={`tab-pane fade ${activeTab ==1 ? 'show active' :''}`} id="posts" role="tabpanel" aria-labelledby="posts-tab">
                                    <div className='lbPosts_Products'>
                                    <Post postData={wallData} postDetail={postDetail} detailPostData={this.state.detailPostData} detailPostShow={(data) => this.detailPostShow(data)} detailProductShow_byref={(postId)=>this.detailProductShow_byref(postId)} />
                                        {/* {wallData?.map((item,i)=>{
                                            return <div className='lb_image_holder' key={i}>
                                            <img className='lb_PPimage' src={item.postFileNew} alt='Post and Product' />
                                        </div>
                                        }) } */}
                                    </div>
                                </div>
                                <div className={`tab-pane fade ${activeTab ==2 ? 'show active' :''}`} id="products" role="tabpanel" aria-labelledby="products-tab">
                                    <div className='lbPosts_Products'>
                                    <Products productData={productData} productDetail={productDetail} detailProductData={this.state.detailProductData} detailProductShow={(data) => this.detailProductShow(data)} detailPostShow_byref={(postId)=>this.detailPostShow_byref(postId)}/>
                                        {/* {productData?.map((product,i)=>{
                                            return <div className='lb_image_holder' key={i}>
                                            <img className='lb_PPimage' src={product.product_image} alt='Post and Product' />
                                        </div>
                                        }) } */}
                                    </div>
                                </div>

                            </div>
                        </div>
                        : <div className='lbPosts_Products'>
                        {wallData && wallData.length ? wallData.map((item,i)=>{
                            return <div className='lb_image_holder' key={i}>
                            <img className='lb_PPimage' src={item.postFileNew} alt='Post and Product' />
                        </div>
                        }): ''
                        // <div > <h4><center> No Posts Found </center></h4></div> 
                        }
                    </div>}

                    {/* <div className='lbUser_actions_inner'>
                        <div className='lbBrowse_product'>
                            <div className='lbBrowsebtn' onClick={()=> this.setState({browseDropDown:!browseDropDown})}>
                                Browse
                            </div>
                        
                            <div className={`lbbrowse_dropdown ${browseDropDown ? 'active' :''}`}>
                                <div className='lbbrowse_option' onClick={()=> this.setState({browseDropDown:false})}>
                                    All Posts
                                </div>
                                <div className='lbbrowse_option' onClick={()=> this.setState({browseDropDown:false})}>
                                    All Products
                                </div>
                            </div>
                        </div>
                        <div className='lbSearch_product'  onClick={()=> this.setState({searchDropDown:true, browseDropDown:false})}>
                            Search
                        </div> 
                        <div className={`lbSearch ${searchDropDown ? 'active' :''}`}>
                            <input className='lbSearchbox' type='text' placeholder='Search...' />
                            <div className='lbSearchClose'   onClick={()=> this.setState({searchDropDown:false})}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill="none">
                                    <path d="M11.4141 10.0001L17.7071 3.70709C17.8946 3.51959 17.9999 3.26527 17.9999 3.00009C17.9999 2.73492 17.8946 2.4806 17.7071 2.29309C17.5196 2.10559 17.2653 2.00024 17.0001 2.00024C16.7349 2.00024 16.4806 2.10559 16.2931 2.29309L10.0001 8.58609L3.70709 2.29309C3.61425 2.20025 3.50403 2.1266 3.38272 2.07635C3.26141 2.02611 3.1314 2.00024 3.00009 2.00024C2.86879 2.00024 2.73877 2.02611 2.61747 2.07635C2.49616 2.1266 2.38594 2.20025 2.29309 2.29309C2.10559 2.4806 2.00024 2.73492 2.00024 3.00009C2.00024 3.26527 2.10559 3.51959 2.29309 3.70709L8.58609 10.0001L2.29309 16.2931C2.10559 16.4806 2.00024 16.7349 2.00024 17.0001C2.00024 17.2653 2.10559 17.5196 2.29309 17.7071C2.4806 17.8946 2.73492 17.9999 3.00009 17.9999C3.26527 17.9999 3.51959 17.8946 3.70709 17.7071L10.0001 11.4141L16.2931 17.7071C16.3857 17.8003 16.4959 17.8742 16.6172 17.9247C16.7386 17.9752 16.8687 18.0011 17.0001 18.0011C17.1315 18.0011 17.2616 17.9752 17.3829 17.9247C17.5043 17.8742 17.6144 17.8003 17.7071 17.7071C17.8 17.6143 17.8738 17.5041 17.9241 17.3828C17.9744 17.2615 18.0003 17.1314 18.0003 17.0001C18.0003 16.8688 17.9744 16.7387 17.9241 16.6174C17.8738 16.4961 17.8 16.3859 17.7071 16.2931L11.4141 10.0001Z" fill="#5C5F62"/>
                                </svg>
                            </div>
                        </div>
                    </div> 
               */}
                </div>
            </div>
            
           
        </div>
    )
  }
}
